import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function grahamNetNetTtm(tableName: string) {
  const title = "Graham Net-Net (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["grahamNetNetTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Value based on net assets minus liabilities.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about Graham Number or other company valuation metrics?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.grahamNetNetTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.grahamNetNetTtm}
        />
      )
    },
  })
}

export { grahamNetNetTtm }
